i.bi {
	margin-right: 0.4rem;
}

.App {
	margin: 0.25rem 0.5rem;
	max-width: 800px;
	padding-bottom: 40px;
	position: relative;
}

.stream-settings {
	display: flex;
	flex-direction: column;
	border: 1px solid #eee;
	box-shadow: 2px 1px 7px #ccc;
	margin-bottom: 0.75rem;
	padding: 0.5rem;
	margin-top: 1rem;
}

.nav-link {
	cursor: pointer;
	padding: 0;
	display: flex;
	align-items: center;
}

.nav-item:not(:first-child) {
	margin-left: 0.25rem;
}

.nav-link.active {
	font-weight: bold;
}

.stream-settings-heading {
	font-weight: bold;
	background-color: rgb(255, 237, 203);
	margin: -0.5rem -0.5rem 0.75rem -0.5rem;
	padding: 0.5rem;
}

.selected-languages {
	display: flex;
}

.selected-language {
	border: 1px solid #eee;
	display: flex;
	align-items: center;
}

.selected-language:not(:first-child) {
	margin-left: 0.5rem;
}

.selected-language-name {
	padding: 0 0.5rem;
	cursor: default;
}

.nav-link-text {
	padding: 0.25rem 0.5rem;
}

.selected-language-delete {
	transition: all 0.2s;
	padding: 0 0.5rem;
}
.selected-language-delete:hover {
	box-shadow: 1px 1px 3px #cdcaca;
}
.selected-language-delete:focus {
	box-shadow: unset;
}

.language-settings-form {
	display: none;
}

.language-settings-form.active {
	display: block;
}
.stream-settings-buttons button:not(:first-child) {
	margin-left: 0.5rem;
}

.copy-from-row {
	max-width: 300px;
}

.status-bar {
	padding: 0.25rem 0.5rem;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	height: 60px;
	display: flex;
	transform: translateY(60px);
	transition: all 0.3s ease-in-out;
	white-space: normal;
	overflow: auto;
}

.status-bar.show {
	transform: translateY(0);
}

.status-bar.error {
	background-color: rgb(255, 142, 142);
	color: white;
}
.status-bar.success {
	background-color: rgb(133, 248, 133);
}

.buttons {
	display: flex;
	margin-top: 0.5rem;
}

.buttons button {
	margin-right: 0.5rem;
}

.languages {
	margin-top: 2rem;
}

.Language {
	display: flex;
	flex-direction: column;
	border: 1px solid #eee;
	box-shadow: 2px 1px 7px #ccc;
	margin-bottom: 0.75rem;
	padding: 0.5rem;
}

.language-header {
	display: flex;
	align-items: center;
	padding: 0.5rem;
	background-color: #f3f3f3;
	margin: -0.5rem -0.5rem 0.5rem;
	transition: all 0.6s;
}

.language-name {
	font-weight: bold;
}

.language-stream-settings {
	display: flex;
	align-items: center;
	margin-left: 1rem;
}

.language-stream-settings-readonly {
	display: flex;
	align-items: center;
}

.language-stream-url {
	font-size: 0.8rem;
}

.Language--live .language-header {
	background-color: #00ffbf;
}

.RangeInput {
	display: flex;
	flex-direction: column;
}

/* .language-settings {
	margin-top: 0.75rem;
} */

.language-settings .RangeInput:not(:first-child) {
	margin-top: 0.5rem;
}

.bi.bi-arrow-clockwise.spin:before {
	animation: spin1 2s linear infinite;
}
